'use strict';

export default class DebuggerBackchannel {
    constructor() {
        this._store = new Map();
        this._index = 0;
    }

    add() {
        this._index++;
        this._store.set(this._index, {});
        return this._index;
    }

    get(index) {
        return this._store.get(index);
    }

    delete(index) {
        this._store.delete(index);
    }

    clear() {
        this._store.clear();
    }
}
